/**
  1. getInitialEmptyStoreFromConfig()
       return initialState & emptyState
  2. loadAsyncFormConfig()
       load the date from API
  3. createReactComponentBaseMap()
       generate the React Components
  4. setFormStore {}
       reducer method for general control components
  5. deleteFormStore {}
       reducer method for general control components
*/


import React from 'react'
import Immutable from 'immutable'

import HorizontalMultiSelector from 'SRC/components/input-components/selector/HorizontalMultiSelector'
import VerticalSingleSelector from 'SRC/components/input-components/selector/VerticalSingleSelector'
import MatrixMultiSelector from 'SRC/components/input-components/selector/MatrixMultiSelector'
import CascadingSelector from 'SRC/components/input-components/cascading-selector/CascadingSelector'
import ChainSelector from 'SRC/components/input-components/chain-selector/ChainSelector'
import CommaSeperatedInputArea from 'SRC/components/input-components/input/CommaSeperatedInputArea'
import Input from 'SRC/components/input-components/input/Input'

export const getInitialEmptyStoreFromConfig = (configs) => {
  const initial = {}
  const empty = {}
  Object.keys(configs).forEach((formKey) => {
    const config = configs[formKey]
    if (config.api) {
      if (typeof config.api === 'string') {
        initial.formConfig = Object.assign({}, initial.formConfig, {
          [config.api]: []
        })
      } else if (Array.isArray(config.api)) {
        const newApis = {}
        for (const api of config.api) {
          newApis[api] = []
        }
        initial.formConfig = Object.assign({}, initial.formConfig, newApis)
      }
    }
    if (config.displayType === 'horizontal-multi-selector') {
      /**
        config = {
          displayType: 'horizontal-multi-selector',
          defaultValueKeys: ['1', '2'],
          valuePool: [
            { key: '1', name: '男' },
            { key: '2', name: '女' }
          ]
        }
        initialState = {
          userSex: [ {key:'1', name:'男'}, { key: '2', name: '女' } ]
        }
        emptyState = {
          userSex: []
        }
      */
      const defaultValueKeysSet = new Set(config.defaultValueKeys)
      initial[formKey] = config.valuePool.filter((x) => (defaultValueKeysSet.has(x.key)))
      empty[formKey] = []
    } else if (config.displayType === 'horizontal-single-selector' ||
               config.displayType === 'vertical-single-selector') {
       /**
         config = {
           displayType: 'horizontal-single-selector',
           defaultValueKeys: ['1'],
           emptyValueKeys: ['1'],
           valuePool: [
             { key: '1', name: '男' },
             { key: '2', name: '女' }
           ]
         }
         initialState = {
           userSex: [ {key:'1', name:'男'} ]
         }
         emptyState = {
           userSex: [ {key:'1', name:'男'} ]
         }
       */
      initial[formKey] = config.valuePool.filter(x => x.key === config.defaultValueKeys[0])
      empty[formKey] = !config.emptyValueKeys ? [] : config.valuePool.filter(x => x.key === config.emptyValueKeys[0])
    } else if (config.displayType === 'matrix-multi-selector') {
      /**
        config = {
          displayType: 'matrix-multi-selector',
          rowKeys: [
            'bindingCouldService'
          ],
          defaultValueKeys: [
            ['bound']
          ],
          valuePool: [
            { key: 'bound', name: '绑定' },
            { key: 'unbound', name: '未绑定' }
          ]
        }
        initialState = {
          bindingAccount: {
            bindingCloudService: [ {key:'bound', name:'绑定'} ]
          }
        }
      */
      const valuePoolMap = new Map(config.valuePool.map((x) => ([x.key, x])))
      initial[formKey] = {}
      config.rowKeys.forEach((formItemKey, index) => {
        if (config.defaultValueKeys[index].length > 0) {
          initial[formKey][formItemKey] = config.defaultValueKeys[index].map((key) => (valuePoolMap.get(key)))
        }
      })
      empty[formKey] = {}
    } else if (config.displayType === 'input') {
      initial[formKey] = config.defalutValue
      empty[formKey] = ''
    } else if (config.displayType === 'comma-seperated-input-area') {
      initial[formKey] = config.defaultValues
      empty[formKey] = []
    } else if (config.displayType === 'cascading-selector') {
      initial[formKey] = config.defaultValues
      empty[formKey] = config.defaultValues.map(() => [])
    } else if (config.displayType === 'customized-selector') {
      initial[formKey] = config.defaultValues
      empty[formKey] = config.emptyValues
    } else if (config.displayType === 'chain-selector') {
      const { initialFrom, emptyForm } = getInitialEmptyStoreFromConfig(config.panelConfig)
      initial[formKey] = [initialFrom.set('relationWithNext', config.relationSelector.valuePool.filter(
        x => x.key === config.relationSelector.defaultValueKeys[0]
      )).toJS()]
      empty[formKey] = [emptyForm.set('relationWithNext', config.relationSelector.valuePool.filter(
        x => x.key === config.relationSelector.defaultValueKeys[0]
      )).toJS()]
    }
  })
  return { initialFrom: Immutable.fromJS(initial), emptyForm: Immutable.fromJS(empty) }
}

export const loadAsyncFormConfig = (formConfig, formCategory, actions) => {
  // Object.keys(formConfig).forEach((formKey) => {
  //   if (formConfig[formKey].api) {
  //     actions.fetchFormConfig(
  //       formCategory,
  //       formKey,
  //       formConfig[formKey].api
  //     )
  //   }
  // })
  Object.keys(formConfig).forEach((formKey) => {
    if (formConfig[formKey].api) {
      let api = formConfig[formKey].api
      if (typeof api === 'string') {
        api = [api]
      }
      api.forEach(x => {
        actions.fetchFormConfig(
          formCategory,
          formKey,
          x
        )
      })
    }
    if (formConfig[formKey].displayType === 'chain-selector') {
      loadAsyncFormConfig(formConfig[formKey].panelConfig, formCategory, actions)
    }
  })
}

export const createReactComponentBaseMap = (
  formCategory,
  formConfig,
  formState,
  formActions,
  createCustomizedComponent,
  contextConfig
) => {
  const childrenMap = new Map()
  Object.keys(formConfig).forEach((formKey) => {
    const config = formConfig[formKey]
    console.log('parse', formCategory)
    let shouldShow = true
    if (config.linkedSeletor && config.linkedSeletor.type === 'hide&show') {
      shouldShow = false
      const registerValueKeysSet = new Set(config.linkedSeletor.registerValueKeys)
      formState[config.linkedSeletor.relatedSeletor].forEach(x => {
        if (registerValueKeysSet.has(x.key)) {
          shouldShow = true
        }
      })
    }
    if (!shouldShow) return

    if (config.displayType === 'horizontal-multi-selector' ||
        config.displayType === 'horizontal-single-selector') {
      console.log('xxxxxxxx', config.name, formKey)
      childrenMap.set(formKey, (
        <HorizontalMultiSelector
          key={formKey}
          icon={config.icon}
          labelSize={config.labelSize}
          multiple={config.displayType === 'horizontal-multi-selector'}
          allowSelectAll={config.allowSelectAll === true}
          disableLabel={config.disableLabel === true}
          label={config.name}
          description={config.description}
          updatePeriod={config.updatePeriod}
          limitation={config.limitation}
          values={config.valuePool}
          selectedValues={formState[formKey]}
          onSelect={formActions.formSet.bind(null, formCategory, formKey)}
          />
      ))
    } else if (config.displayType === 'vertical-single-selector') {
      childrenMap.set(formKey, (
        <VerticalSingleSelector
          key={formKey}
          label={config.name}
          icon={config.icon}
          placeholder={config.placeholder}
          disableLabel={config.disableLabel === true}
          value={formState[formKey]}
          options={config.valuePool}
          onSelect={formActions.formSet.bind(null, formCategory, formKey)}
          />
      ))
    } else if (config.displayType === 'matrix-multi-selector') {
      childrenMap.set(formKey, (
        <MatrixMultiSelector
          key={formKey}
          columnHeader={config.columnHeader}
          disableHeader={config.disableHeader}
          columnNum={config.columnNum}
          selectorPos={config.selectorPos}
          rowKeys={config.rowKeys}
          rows={config.rows}
          values={config.valuePool}
          selectedValues={formState[formKey]}
          onSelect={formActions.formSet.bind(null, formCategory, formKey)}
          style={{ width: '800px' }}
          />
      ))
    } else if (config.displayType === 'cascading-selector') {
      childrenMap.set(formKey, (
        <CascadingSelector
          key={formKey}
          label={config.name}
          icon={config.icon}
          disableLabel={config.disableLabel}
          selectorLabels={config.selectorLabels}
          disableSelectorLabel={config.disableSelectorLabel}
          placeholders={config.placeholders}
          selectorType={config.selectorType}
          selectedValues={formState[formKey]}
          valuePool={config.valuePool}
          onSelect={formActions.formSet.bind(null, formCategory, formKey)}
          />
      ))
    } else if (config.displayType === 'input') {
      childrenMap.set(formKey, (
        <Input
          key={formKey}
          icon={config.icon}
          label={config.name}
          disableLabel={config.disableLabel}
          placeholder={config.placeholder}
          value={formState[formKey]}
          onInput={formActions.formSet.bind(null, formCategory, formKey)}
          />
      ))
    } else if (config.displayType === 'comma-seperated-input-area') {
      childrenMap.set(formKey, (
        <CommaSeperatedInputArea
          key={formKey}
          icon={config.icon}
          label={config.name}
          disableLabel={config.disableLabel}
          placeholder={config.placeholders}
          values={formState[formKey]}
          onInput={formActions.formSet.bind(null, formCategory, formKey)}
          />
      ))
    } else if (config.displayType === 'chain-selector') {
      childrenMap.set(formKey, (
        <ChainSelector
          key={formKey}
          hanging={config.style === 'hanging'}
          createCustomizedComponent={createCustomizedComponent || {}}
          createReactComponentBaseMap={createReactComponentBaseMap}
          formCategory={formKey}
          formConfig={config.panelConfig}
          contextConfig={contextConfig}
          relationSelector={config.relationSelector}
          formState={formState[formKey]}
          onSelect={formActions.formSet.bind(null, formCategory, formKey)}
          />
      ))
    } else if (config.displayType === 'customized-selector' ||
               config.displayType === 'customized-selector-without-state') {
      if (!createCustomizedComponent || !createCustomizedComponent[formKey]) {
        console.error(`Failed to load createCustomizedComponent[${formKey}]()`)
      } else {
        childrenMap.set(formKey, createCustomizedComponent[formKey](formConfig, formState, formActions, contextConfig))
      }
    } else {
      childrenMap.set(formKey, null)
    }
  })
  return childrenMap
}

export const setFormStore = {
  /**
    state = Immutable.fromJS({
      userSex: [ {key:'1', name:'男'} ]
    })
    action = {
      category: 'basicProperties',
      key: 'userSex',
      values: [ {key:'2', name: '女'} ]
    }
    return: Immutable.fromJS({
      userSex: [ {key:'2', name: '女'} ]
    })
  */
  /* eslint-disable no-param-reassign */
  'horizontal-multi-selector': (state, action, config) => {
    state = state.set(action.formKey, Immutable.fromJS(action.values))
    // Setting The Corelated 'horizontal-multi-selector'
    if (config && config[action.formKey].linkedSeletor) {
      if (config[action.formKey].linkedSeletor.type === 'category') {
        const { role, relatedSeletor } = config[action.formKey].linkedSeletor
        if (role === 'parent') {
          const activeCategorySet = new Set(state.get(action.formKey).toJS().map((x) => (x.key)))
          state = state.set(relatedSeletor, Immutable.fromJS(
            config[relatedSeletor].valuePool.filter((x) => (
              activeCategorySet.has(x.category)
            ))
          ))
        } else {
          const activeCategorySet = new Set(state.get(action.formKey).toJS().map((x) => (x.category)))
          state = state.set(relatedSeletor, Immutable.fromJS(
            config[relatedSeletor].valuePool.filter((x) => (
              activeCategorySet.has(x.key)
            ))
          ))
        }
      }
    }
    return state
  },
  'horizontal-single-selector': (state, action, config) => (
    setFormStore['horizontal-multi-selector'](state, action, config)
  ),
  'vertical-single-selector': (state, action, config) => (
    setFormStore['horizontal-multi-selector'](state, action, config)
  ),
  /**
    state = Immutable.fromJS({
      bindingAccount: {
        bindingCloudService: [ {key:'bound', name:'绑定'} ]
      }
    })
    action = {
      category: 'boundProperties',
      key: 'bindingAccount',
      values: [
        'bindingCloudService',
        [{key:'unbound', name:'未绑定'}]
      ]
    }
    return: Immutable.fromJS({
      bindingAccount: {
        bindingCloudService: [{key:'unbound', name:'未绑定'}]
      }
    })
  */
  'matrix-multi-selector': (state, action) => (
    state.update(action.formKey, (oldValue) => (
      oldValue.set(action.values[0], Immutable.fromJS(action.values[1]))
    ))
  ),
  'input': (state, action) => ( // eslint-disable-line quote-props
    state.set(action.formKey, action.values)
  ),
  'comma-seperated-input-area': (state, action) => (
    state.set(action.formKey, Immutable.fromJS(action.values))
  ),
  'cascading-selector': (state, action) => (
    state.set(action.formKey, Immutable.fromJS(action.values))
  ),
  'chain-selector': (state, action, config, emptyState) => {
    switch (action.values.type) {
      case 'CHAIN_SELECTOR_ADD':
        return state.update(action.formKey, (oldArray) => (
          oldArray.push(emptyState.get(action.formKey).get(0))
        ))
      case 'CHAIN_SELECTOR_DELETE':
        if (state.get(action.formKey).size === 1) {
          return state.set(action.formKey, emptyState.get(action.formKey))
        }
        return state.update(action.formKey, (oldArray) => (
          oldArray.pop()
        ))
      case 'CHAIN_SELECTOR_SELECT_RELATION':
        return state.update(action.formKey, (oldStateArray) => (
          oldStateArray.update(action.values.index, (oldState) => (
            oldState.set('relationWithNext', action.values.relation)
          ))
        ))
      case 'CHAIN_SELECTOR_UPDATE':
        // update store
        return state.update(action.formKey, (oldStateArray) => (
          oldStateArray.update(action.values.action.formCategory, (oldState) => {
            const subAction = action.values.action
            const displayType = config[action.formKey].panelConfig[subAction.formKey].displayType
            if (setFormStore[displayType]) {
              return setFormStore[displayType](oldState, subAction, config[action.formKey].panelConfig)
            }
            return oldState
          })
        ))
      default:
        return state
    }
  }
}

export const deleteFormStore = {
  /**
    state = Immutable.fromJS({
      userSex: [ {key:'1', name:'男'}, {key:'2', name:'女'} ]
    })
    action = {
      category: 'basicProperties',
      key: 'userSex',
      values: [ {key:'1', name:'男'} ]
    }
    return: Immutable.fromJS({
      userSex: [ {key:'2', name:'女'} ]
    })
  */
  'horizontal-multi-selector': (state, action, config, emptyState) => {
    if (!action.values) {
      // Clear Whole Value Items
      state = state.set(action.formKey, emptyState ? emptyState.get(action.formKey) : Immutable.List())
    } else {
      // Clear Specific Value Item
      const deletedKeysSet = new Set(action.values.map((x) => (x.key)))
      state = state.update(action.formKey, (oldValue) => (
        oldValue.filter((x) => (!deletedKeysSet.has(x.get('key'))))
      ))
    }
    // Setting The Corelated 'horizontal-multi-selector'
    if (config && config[action.formKey].linkedSeletor) {
      if (config[action.formKey].linkedSeletor.type === 'category') {
        const { role, relatedSeletor } = config[action.formKey].linkedSeletor
        if (role === 'parent') {
          const activeCategorySet = new Set(state.get(action.formKey).toJS().map((x) => (x.key)))
          state = state.update(relatedSeletor, (oldArray) => (
            oldArray.filter(x => activeCategorySet.has(x.get('category')))
          ))
        } else {
          const activeCategorySet = new Set(state.get(action.formKey).toJS().map((x) => (x.category)))
          state = state.set(relatedSeletor, Immutable.fromJS(
            config[relatedSeletor].valuePool.filter((x) => (
              activeCategorySet.has(x.key)
            ))
          ))
        }
      }
    }
    return state
  },
  'horizontal-single-selector': (state, action, config, emptyState) => (
    deleteFormStore['horizontal-multi-selector'](state, action, config, emptyState)
  ),
  'vertical-single-selector': (state, action, config, emptyState) => (
    deleteFormStore['horizontal-multi-selector'](state, action, config, emptyState)
  ),
  /**
    state = Immutable.fromJS({
      bindingAccount: {
        bindingCloudService: [ {key:'bound', name:'绑定'}, {key:'unbound', name:'未绑定'} ]
      }
    })
    action = {
      category: 'boundProperties',
      key: 'bindingAccount',
      values: [
        'bindingCloudService',
        [{key:'unbound', name:'未绑定'}]
      ]
    }
    return: Immutable.fromJS({
      bindingAccount: {
        bindingCloudService: [{key:'bound', name:'绑定'}]
      }
    })
  */
  'matrix-multi-selector': (state, action) => {
    // Clear whole rows
    if (!action.values || !action.values[0]) {
      return state.set(action.formKey, Immutable.Map())
    }
    // Clear one row
    if (!action.values[1]) {
      return state.update(action.formKey, (oldValue) => (
        oldValue.set(action.values[0], Immutable.List())
      ))
    }
    // Clear Specific Value Item Of One Row
    const deletedKeysSet = new Set(action.values[1].map((x) => (x.key)))
    return state.update(action.formKey, (oldDict) => (
      oldDict.update(action.values[0], Immutable.List(), (oldList) => (
        oldList.filter((x) => (!deletedKeysSet.has(x.get('key'))))
      ))
    ))
  },
  'input': (state, action) => ( // eslint-disable-line quote-props
    state.set(action.formKey, '')
  ),
  'comma-seperated-input-area': (state, action) => (
    state.set(action.formKey, Immutable.List())
  ),
  'chain-selector': (state, action, config, emptyState) => (
    state.update(action.formKey, (oldArray) => (
      oldArray.set(action.values, emptyState.get(action.formKey).get(0))
    ))
  ),
  'cascading-selector': (state, action) => (
    state.update(action.formKey, (oldList) => (
      oldList.map((value, index) => {
        if (index < action.values[0]) return value
        else if (index === action.values[0]) {
          const deletedKeysSet = new Set(action.values[1].map(x => x.key))
          return value.filter(x => !deletedKeysSet.has(x.get('key')))
        }
        return Immutable.List()
      })
    ))
  )
}



